.app__page {
    display: flex;
}
.header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;

    /*to make a sticky header*/
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: white;
}

.header__logo {
    height: 21px;
    object-fit: contain;
    margin-left: 65px;
}

.header__input {
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid lightgray;
}

.header__icon {
    margin-right: 25px;
    font-size: 25px !important;
}

.header__keyborad{
    margin-right: 10px;
    color: gray;
}

.header__inputButton {
    margin-top: 4px;
    font-size: 20px !important;
    width: 65px !important;
    background-color: #fafafa00;
    color: gray
}

.header__inputButton:hover {
    color: black;
}

.header__inputWrapper {
    border: none;
    border-left: 1px solid lightgray;
    background-color:  rgb(247, 247, 247);
}

.header__inputWrapper:hover {
    background-color:  rgba(240, 238, 238, 0.918);
}

.header__input > input {
    flex: 1 1;
    border: none;
    height: 25px;
}

.header__icons {
    display: flex;
    align-items: center;
    color: #615e5e;
}

.header__tooltipText {
    /* visibility: hidden; */
    width: 72px;
    background-color: #555;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;
    font-size: 12px;
  
    /* Position the tooltip text */
    position: absolute;
    z-index: 1;
    margin-left: -21px;
    
    /* Fade in tooltip */
    opacity: 0;
    transition: opacity 0.3s;
    transition-timing-function: ease-in-out;
  }
  
  /* Show the tooltip text when you mouse over the tooltip container */
  .header__tooltipWrapper:hover .header__tooltipText {
    visibility: visible;
    opacity: 1;
  }




.sidebar__hidden:not(.icons) {
    flex: 0 1;
    display: none;
    transition: 850ms;

}

.sidebar {
    flex: 0.16 1;
    transition: 350ms;
}

.icons {
    position: fixed;
    z-index: 100;
    margin: -44px 0px 0 20px;
    cursor: pointer;
}

a {
    text-decoration: none;
    color: black;
}

.sidebar > hr {
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
}


.sidebarRow{
    display: flex;
    align-items: center;
    padding: 4px 20px;
}

.sidebarRow__icon {
    color: #606060;
    font-size: 22px !important;
    margin-top: 5px;
}

.sidebarRow__title {
    flex: 1 1;
    margin-left: 20px;
    font-size: 14px;
    font-weight: 500;
}

.sidebarRow:hover {
    background-color: #f2f2f2;
    cursor: pointer;
}

/*change the color of icon and font-weight of title when mouse hover*/
.sidebarRow:hover > .sidebarRow__icon {
    color: red;
}

.sidebarRow:hover > .sidebarRow__title {
    font-weight: bold;
}

/*if the container has selected tag change the color*/
.sidebarRow.selected {
    background-color: #e6e6e6;
}

.sidebarRow.selected > .sidebarRow__icon {
    color: red;
}

.sidebarRow.selected > .sidebarRow__title {
    font-weight: bold;
}


.subscriptionRow{
    display: flex;
