.projectRow__imgText {
    /*font */
    color: white;
    font-size: 50px;
    font-family: 'Montserrat', sans-serif;

    /*to center the img text*/
    justify-content: center;
    align-items: center;
    display: flex;

    /*to be in the right position*/
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    
    background: rgba(53, 53, 53, 0.89);
    width: 357px;
    height: 190px;
    opacity: 0;

    /*animation effect*/
    transition: all 300ms ease-in-out;

    /*hide text when not hovering*/
    visibility: hidden;
  }
  
  .projectRow__video:hover .projectRow__imgText {
    -webkit-opacity: 1;
    opacity: 1;
    visibility: visible;
    font-size: 35px;
